:root {
  --accent-color: #c42e2e;
  --accent-shadow: -1px -1px 49px 3px #c42e2ebf;
  --primary-color: #373d40;
  --color-grey: #f3f5f6;
  --color-white: #e0dedc;
  --cubic: cubic-bezier(.4, 0, .2, 1);
  --primary-font: "Wix Madefor Display";
  --title-font: "Cormorant Infant";
}

.header {
  padding: 10px 0;
}

.header__wrapper {
  justify-content: center;
  display: flex;
}

.header__logo-wrapper {
  margin: auto 0;
}

.header__navigation {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__navigation-list {
  transition: .5s var(--cubic);
  font-weight: bold;
  display: flex;
}

.header__navigation-list li:not(:last-child) {
  margin-right: 40px;
}

.header__navigation-list li {
  transition: .5s var(--cubic);
  margin-right: 40px;
  padding: 14px 0;
  position: relative;
}

.header__navigation-list li:before {
  content: "";
  background: var(--accent-color);
  transition: all .5s;
  transition: .5s var(--cubic);
  border-radius: 5px;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(0);
}

.header__navigation-list li:hover:before {
  transition: .5s var(--cubic);
  transform: scale(1);
}

.header__navigation-item {
  font-size: 20px;
  font-weight: 800;
}

.header__navigation-item a {
  transition: .5s var(--cubic);
  padding: 14px 0;
}

.header__navigation-item a:hover {
  transition: .5s var(--cubic);
  color: var(--accent-color);
}

.header__social {
  margin: auto 0;
}

.header__social-items {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__social-items li:not(:last-child) {
  margin-right: 14px;
}

.header__social-item {
  cursor: pointer;
  transition: .5s var(--cubic);
  border: 2px solid;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  display: flex;
}

.header__social-item:hover {
  box-shadow: var(--accent-shadow);
  border: var(--accent-color) solid 2px;
  fill: var(--accent-color);
  transition: .5s var(--cubic);
}

.header__social-item:hover .header__svg {
  transition: .5s var(--cubic);
  fill: var(--accent-color);
}

.header__svg {
  transition: .5s var(--cubic);
  fill: currentColor;
}

